<template>
</template>

<script>
    import LabBaseFirebase from './LabBaseFirebase'
    import LabBaseMinio from './LabBaseMinio'


    export default {
        name: "LabBaseFirebase",
        mixins: [LabBaseFirebase, LabBaseMinio],
        methods: {

            _list(path, metadata) {

                if(path.endsWith("...")) 
                    return this.minio_list(path, metadata);
                else
                    return this.firebase_list(path, metadata);

            },

            async _get(path) {
                if(path.endsWith("...")) 
                    return this.minio_get(path);
                else
                    return this.firebase_get(path);
            },

            _put(path, string) {
                if(path.endsWith("...")) 
                    return this.minio_put(path);
                else
                    return this.firebase_put(path);
            },
            _delete(path) {
                if(path.endsWith("...")) 
                    return this.minio_delete(path);
                else
                    return this.firebase_delete(path);
            },


        }
    };
</script>
